img {
  margin-top: 20px;
  max-width: 100%;
  border: 2px solid #333;
}
.img-container {
  position:relative;
  height:724px;
  width:425px;
}
.img-container:before{
  content:"";
  position:absolute;
  top:0;  
  left:0;  
  background-image:url(./img/banner.png);
  background-repeat:no-repeat;
  height:100%;
  width:100%;

}
.img-container video {
  z-index: -1;
  position: absolute;
  top: -2%;
  left: 50%;
  transform: translate(-50%,50%);
  width: 340px;
}
